import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import {
  Box,
  Card,
  CardContent,
  Typography,
  Button,
  CircularProgress,
  Stack,
  Grow,
} from "@mui/material";
import TrainIcon from "@mui/icons-material/Train";
import AccountBalanceIcon from "@mui/icons-material/AccountBalance";
import ArrowForwardIcon from "@mui/icons-material/ArrowForward";
import { irctcAgentLogin } from "../../api/login/Login";

const IrctcAgentLogin = () => {
  const navigate = useNavigate();
  const [loading, setLoading] = useState(true);
  const [magicLink, setMagicLink] = useState("");
  const [error, setError] = useState("");
  useEffect(() => {
    const fetchLink = async () => {
      try {
        const res = await irctcAgentLogin();
        const link = res?.data?.magicLink || "";
        if (!link) {
           setError(
            "IRCTC server is currently unavailable. Please try again later."
          );
          return;
        }
        setMagicLink(link);
      } catch (err) {
        console.error("Agent login error", err);
        setError("Unable to connect to IRCTC. Please try again later.");
      } finally {
        setLoading(false);
      }
    };
    fetchLink();
  }, []);

  return (
    <Box
      sx={{
        minHeight: "100vh",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        background:
          "linear-gradient(135deg, #0f2027 0%, #203a43 50%, #2c5364 100%)",
      }}
    >
      <Grow in>
        <Card
          sx={{
            width: 420,
            borderRadius: 4,
            boxShadow: "0 25px 50px -12px rgba(0,0,0,0.45)",
            overflow: "hidden",
          }}
        >
          <Box
            sx={{
              height: 6,
              background: "#cc9840",
            }}
          />
          <CardContent sx={{ p: 4, textAlign: "center" }}>
            {loading && (
              <>
                <CircularProgress sx={{ mb: 2 }} />
                <Typography sx={{ color: "#90a4ae" }}>
                  Preparing IRCTC access…
                </Typography>
              </>
            )}
            {!loading && error && (
              <>
                <Typography variant="h6" color="error" sx={{ mb: 2 }}>
                  {error}
                </Typography>

                <Button
                  fullWidth
                  variant="contained"
                  sx={{ mt: 1.5, background: "#cc9840" }}
                  onClick={() => navigate("/")}
                >
                  Go Back to Dashboard
                </Button>
              </>
            )}
            {!loading && !error && (
              <>
                <Stack
                  direction="row"
                  justifyContent="center"
                  spacing={2}
                  mb={3}
                >
                  <Box
                    sx={{
                      width: 48,
                      height: 48,
                      borderRadius: 2.5,
                      background:
                        "linear-gradient(135deg,#1a237e,#3949ab)",
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "center",
                      boxShadow:
                        "0 6px 20px rgba(26,35,126,0.35)",
                    }}
                  >
                    <AccountBalanceIcon sx={{ color: "#fff" }} />
                  </Box>
                  <Typography
                    sx={{
                      fontSize: 20,
                      fontWeight: 300,
                      color: "#90a4ae",
                    }}
                  >
                    ×
                  </Typography>
                  <Box
                    sx={{
                      width: 48,
                      height: 48,
                      borderRadius: 2.5,
                      background:
                        "#cc9840",
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "center",
                      boxShadow:
                        "0 6px 20px rgba(255,107,0,0.35)",
                    }}
                  >
                    <TrainIcon sx={{ color: "#fff" }} />
                  </Box>
                </Stack>
                <Typography
                  variant="h6"
                  sx={{ fontWeight: 600, mb: 1 }}
                >
                  Connect to IRCTC
                </Typography>
                <Typography
                  sx={{
                    fontSize: 14,
                    color: "#90a4ae",
                    mb: 3,
                  }}
                >
                  You’ll be securely redirected to IRCTC to continue
                </Typography>
                <Button
                  fullWidth
                  size="large"
                  variant="contained"
                  onClick={() => window.open(magicLink, "_blank", "noopener,noreferrer")}
                  endIcon={<ArrowForwardIcon />}
                  sx={{
                    py: 1.6,
                    borderRadius: 2,
                    fontSize: 15,
                    fontWeight: 600,
                    textTransform: "none",
                    background:
                      "#cc9840",
                    boxShadow:
                      "0 10px 28px rgba(255,107,0,0.4)",
                    "&:hover": {
                      background:
                        "#d29731ff",
                      boxShadow:
                        "0 14px 36px rgba(255,107,0,0.5)",
                    },
                  }}
                >
                  Continue to IRCTC
                </Button>
                <Typography
                  sx={{
                    mt: 3,
                    fontSize: 12,
                    color: "#90a4ae",
                  }}
                >
                  Secure redirection • No data stored locally
                </Typography>
              </>
            )}
          </CardContent>
        </Card>
      </Grow>
    </Box>
  );
};

export default IrctcAgentLogin;